export default {
	data() {
		return {
			article: [],
			webview: '',
		};
	},
	onLoad(option) {
		if (option.url) {
			this.webview = option.url
		} else if (option.id) {
			this.getArticle(option.id)
		}
	},
	methods: {
		getArticle(id) {
			this.$app.request(this.$app.API.ARTICLE, {
				id
			}, res => {

				try {
					this.article = JSON.parse(res.data.value)
				} catch (e) {
					this.webview = res.data.value
				}

				uni.setNavigationBarTitle({
					title: res.data.name
				});
			})
		}
	},
}
